























































export default {
	data() {
		return {
			list:[],
		}
	},
	onLoad() {
		this.getAgentTdph();
	},
	onShow() {
	},
	methods: {
		getAgentTdph(){
			try {
				this.$http.post(this.websiteUrl+'/api/agent/getAgentTdph',{},(res) => {
					if(res.data.code==1){
						this.list = res.data.result;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
			
		}
	},
	components: {
		
	}
}
